@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html *::-webkit-scrollbar {
  border-radius: 0;
  width: 8px;
  height: 8px;
}

html *::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(22, 24, 35, 0.06);
}

html *::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0);
}

body {
  line-height: 1.5;
  font-size: 16px;
  font-weight: 500;
  color: #000;
  background-color: #fff;
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
}

button {
  border: none;
  outline: none;
}

.main {
  margin-top: 55px;
}
.main.active .right_panel {
  margin-left: 90px;
}
.main .right_panel {
  margin-top: 55px;
  margin-left: 280px;
  background-color: rgba(0, 0, 0, 0.02);
  transition: margin 0.1s;
}
.main .right_panel > .content {
  min-height: calc(100vh - 55px);
  padding: 20px;
}

.dropdown {
  position: relative;
}
.dropdown::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: transparent;
  height: 10px;
  z-index: 99;
}
.dropdown.active .menu {
  opacity: 1;
  visibility: visible;
}
.dropdown .menu {
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 100px;
  background-color: #fff;
  border-radius: 8px;
  z-index: 99;
  box-shadow: 1px 0 20px rgba(0, 0, 0, 0.08);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3 ease;
}

.page_title {
  padding: 4px 10px;
  border-radius: 4px;
  background-color: #fff;
  color: rgba(0, 0, 0, 0.5);
  text-transform: uppercase;
  font-weight: 700;
}

.btn {
  line-height: 1;
  display: inline-block;
  color: inherit;
  padding: 10px 14px;
  border-radius: 6px;
  font-size: 14px;
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease;
}
.btn.btn_secondary {
  color: #000;
  background-color: #ccc;
}
.btn.btn_secondary:hover {
  background-color: rgba(0, 0, 0, 0.5);
}
.btn.btn_primary {
  color: #fff;
  background-color: #000;
  border: 1px solid;
}
.btn.btn_primary:hover {
  background-color: rgba(0, 0, 0, 0.5);
}
.btn_cancel, .btn_confirm, .btn_edit, .btn_show, .btn_delete {
  padding: 6px 12px;
  border: 1px solid;
  background-color: #fff;
}
.btn_cancel i, .btn_confirm i, .btn_edit i, .btn_show i, .btn_delete i {
  font-size: 12px !important;
}
.btn_show {
  color: rgb(86, 86, 34);
}
.btn_show:hover {
  background-color: rgb(86, 86, 34);
  color: #fff;
}
.btn_edit {
  color: orange;
}
.btn_edit:hover {
  background-color: orange;
  color: #fff;
}
.btn_cancel, .btn_delete {
  color: red;
}
.btn_cancel:hover, .btn_delete:hover {
  background-color: red;
  color: #fff;
}
.btn_confirm {
  color: blue;
}
.btn_confirm:hover {
  background-color: blue;
  color: #fff;
}

.modal {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  transition: opacity 0.7s ease, visiblity 0.7s ease;
  z-index: 99;
}
.modal.show {
  opacity: 1;
  visibility: visible;
}
.modal.show .modal_container {
  top: 50%;
  opacity: 1;
  visibility: visible;
}
.modal_container {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #333333;
  background: #ffffff;
  min-width: 350px;
  padding: 22px 0;
  transition: all 0.3s ease;
  opacity: 0;
  visibility: hidden;
  border-radius: 6px;
  box-shadow: 1px 0 20px rgba(0, 0, 0, 0.08);
}
.modal_inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: inherit;
}
.modal_title {
  margin-bottom: 10px;
  color: #000;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.5px;
}
.modal_text {
  margin-bottom: 20px;
  color: #607d8b;
}
.modal .btn-close {
  position: absolute;
  right: 6px;
  top: 0px;
  cursor: pointer;
  background-color: #fff;
  opacity: 0.5;
  transition: opacity 0.3s ease;
  padding: 10px;
}
.modal .btn-close:hover {
  opacity: 1;
}
.modal .btn-close i {
  font-size: 22px;
}

.mt-1 {
  margin-top: 4px;
}

.mt-2 {
  margin-top: 8px;
}

.mt-3 {
  margin-top: 12px;
}

.mt-4 {
  margin-top: 16px;
}

.mt-5 {
  margin-top: 20px;
}

.ml-2 {
  margin-left: 8px;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.d-inline {
  display: inline-block;
}

.d-block {
  display: block !important;
}

.text-color {
  color: #607d8b;
}

.loader {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: inline-block;
  border-top: 3px solid #000;
  border-right: 3px solid transparent;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.header {
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  border-bottom: 1px solid #e8e9ed;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  background-color: #fff;
}
.header_left {
  width: 350px;
  display: flex;
  align-items: center;
}
.header_logo {
  min-width: 250px;
  margin-right: 16px;
  text-align: center;
}
.header_logo .logo_link {
  text-transform: uppercase;
  color: #000;
  font-size: 20px;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  font-weight: 600;
  text-shadow: 5px 5px rgba(0, 0, 0, 0.1);
}
.header_logo .logo_link img {
  width: 30px;
  height: auto;
  object-fit: cover;
}
.header .button-toggle-sidebar {
  width: 40px;
  line-height: 1;
  text-align: right;
  user-select: none;
}
.header .button-toggle-sidebar i {
  font-size: 18px;
  color: #607d8b;
  padding: 10px;
  cursor: pointer;
}
.header_right {
  flex: 1;
  display: flex;
  justify-content: end;
  height: 100%;
}
.header_right .user {
  display: flex;
  align-items: center;
  height: 100%;
  gap: 10px;
  color: #607d8b;
  cursor: pointer;
  user-select: none;
}
.header_menu {
  position: absolute;
  top: calc(100% + 10px);
  right: 50px;
  width: 200px;
  background-color: #fff;
  padding: 10px 0;
  border-radius: 8px;
  z-index: 99;
  box-shadow: 1px 0 20px rgba(0, 0, 0, 0.08);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3 ease;
}
.header_menu::after {
  content: "";
  position: absolute;
  right: 20px;
  top: -26px;
  border-width: 16px 20px;
  border-style: solid;
  border-color: transparent transparent #fff transparent;
  cursor: pointer;
}
.header_menu .menu_item {
  list-style: none;
}
.header_menu .menu_item:hover .menu_link {
  color: #000;
  background-color: #fafafa;
}
.header_menu .menu_link {
  color: #607d8b;
  font-size: 14px;
  display: block;
  padding: 8px 10px;
  transition: all 0.3s ease;
}
.header_menu .menu_link i {
  margin-left: 10px;
}

.sidebar {
  position: fixed;
  top: 55px;
  height: calc(100vh - 55px);
  left: 0;
  width: 280px;
  background-color: #fff;
  box-shadow: 1px 0 20px rgba(0, 0, 0, 0.08);
  overflow-y: auto;
}
.sidebar .menu {
  margin-top: 20px;
}
.sidebar .menu_item {
  list-style: none;
  color: #607d8b;
  font-size: 14px;
  line-height: 50px;
  padding: 0 30px;
  transition: all 0.3s ease;
}
.sidebar .menu_item:hover {
  background-color: #fafafa;
}
.sidebar .menu_item:hover .menu_link {
  color: #000;
}
.sidebar .menu_item.active {
  background-color: #fafafa;
  border-right: 2px solid rgba(0, 0, 0, 0.5);
}
.sidebar .menu_item.active .menu_link {
  color: #000;
}
.sidebar .menu_item.menu_item-title {
  color: #41434d;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 50px;
  user-select: none;
  background-color: #fff !important;
}
.sidebar .menu_link {
  color: inherit;
  display: block;
  font-size: inherit;
  line-height: inherit;
  font-weight: 400;
  letter-spacing: 0.2px;
  transition: all 0.3s ease;
}
.sidebar .menu_link .icon {
  display: inline-block;
  width: 55px;
  text-align: left;
  transition: width 0.2s;
}

.main.active .sidebar {
  width: auto;
}
.main.active .sidebar .menu_item-title {
  display: none;
}
.main.active .sidebar .menu_link .icon {
  width: 30px;
  font-size: 20px;
  text-align: center;
}
.main.active .sidebar .menu_link .text {
  display: none;
}

.table {
  color: #607d8b;
}
.table_add {
  font-size: 16px;
  letter-spacing: 0.5px;
}
.table_title h1 {
  font-size: 14px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.6);
  text-transform: uppercase;
}
.table_action {
  margin-top: 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.table_tab {
  display: flex;
  gap: 10px;
}
.table_tab li {
  padding-bottom: 8px;
}
.table_tab li:hover a {
  color: rgba(0, 0, 0, 0.6);
}
.table_tab li.active {
  border-bottom: 2px solid rgba(0, 0, 0, 0.6);
  color: rgba(0, 0, 0, 0.6);
}
.table_tab a {
  color: inherit;
  font-size: 14px;
  line-height: 1;
  display: inline-block;
  transition: all 0.3s ease;
}
.table_totalItem {
  font-size: 14px;
}
.table_totalItem b {
  font-size: 16px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.6);
}
.table_main {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: #fff;
  margin-top: 12px;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
}
.table_main.animation .top_table,
.table_main.animation .table_content {
  opacity: 0;
  visibility: hidden;
}
.table_main.animation .table_loader {
  opacity: 1;
  visibility: visible;
}
.table_loader {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3 ease;
}
.table .top_table {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 16px;
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease;
}
.table_search {
  position: relative;
}
.table_search input {
  border: 1px solid rgba(0, 0, 0, 0.2);
  outline: none;
  padding: 10px 12px 10px 40px;
  border-radius: 6px;
  font-size: 14px;
  letter-spacing: 0.5px;
  width: 392px;
  transition: all 0.3s ease;
}
.table_search input:focus {
  border-color: #000;
}
.table_search i {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
}
.table_content {
  position: relative;
  height: calc(100vh - 230px);
  overflow-y: auto;
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease;
}
.table_content table {
  width: 100%;
  min-width: 900px;
  table-layout: fixed;
  border-collapse: collapse;
  text-align: left;
}
.table_content table thead {
  position: sticky;
  top: 0;
  text-align: center;
  background-color: #fefafa;
  z-index: 9;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 1px -2px, rgba(0, 0, 0, 0.2) 0px 0px 0px 1px inset;
}
.table_content table thead th {
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 500;
  color: #607d8b;
  font-size: 12px;
}
.table_content table tbody tr {
  transition: all 0.2s ease;
  border-bottom: 1px solid #f3f3f3;
}
.table_content table tbody tr:hover {
  background-color: rgba(0, 0, 0, 0.05) !important;
}
.table_content table tbody tr:nth-of-type(even) {
  background-color: #fcfcfc;
}
.table_content table tbody td {
  padding: 10px 20px;
  color: #607d8b;
  font-size: 14px;
}
.table_content table tbody td:first-child {
  text-align: center;
}
.table_content table tbody td:last-child {
  text-align: center;
  white-space: nowrap;
}
.table_content table tbody td:last-child i {
  font-size: 18px;
}
.table_content table tbody td img {
  margin: 0 auto;
  width: 50px;
  border-radius: 4px;
  border: 3px solid #ccc;
}
.table_dropdown span {
  padding: 10px 22px;
  cursor: pointer;
}
.table_dropdown .menu {
  top: 80%;
  left: calc(50% + 12px);
  text-align: left;
  padding: 10px;
  z-index: 1;
}
.table_dropdown .menu_item {
  list-style: none;
}
.table_dropdown .menu_item:hover .menu_link {
  color: #000;
  background-color: #fafafa;
}
.table_dropdown .menu_link {
  color: #607d8b;
  font-size: 14px;
  display: block;
  padding: 8px 10px;
  transition: all 0.3s ease;
  border-radius: 6px;
}
.table_dropdown .menu_link i {
  margin-left: 10px;
}

.form {
  max-height: calc(100vh - 200px);
  overflow-y: auto;
  padding: 10px 30px;
}
.form_group + .form_group {
  margin-top: 8px;
}
.form_group.invalid .form_input {
  border-color: #ed1c24;
}
.form_field {
  position: relative;
}
.form_input {
  padding: 12px 20px;
  border: 1px solid #e8e9ed;
  border-radius: 4px;
  width: 100%;
  transition: 0.25s ease;
  outline: none;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.4;
}
.form_input[type=radio] {
  width: auto;
}
.form_input:hover {
  border-color: #ccc;
}
.form_input:focus {
  border-color: #ccc;
}
.form_input:not(:placeholder-shown) + .form_label, .form_input:focus + .form_label {
  top: 0;
  padding: 0 10px;
  display: inline-block;
  background-color: white;
  color: #607d8b;
}
.form_label {
  position: absolute;
  top: 50%;
  left: 21px;
  transform: translateY(-50%);
  user-select: none;
  color: #607d8b;
  pointer-events: none;
  transition: 0.25s ease;
  font-size: 14px;
  font-weight: 500;
}
.form_messages {
  margin: 4px 0 0 8px;
  font-size: 14px;
  font-weight: 400;
  color: #000;
  display: inline-block;
  letter-spacing: 0.5px;
  padding-bottom: 8px;
}

#toast {
  position: fixed;
  top: 50px;
  right: 32px;
  z-index: 99;
}

.toast {
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 6px;
  border-left: 4px solid;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 20px 0;
  margin: 0 auto;
  transition: all 0.5s ease;
}
@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(calc(100% + 32px));
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fadeOut {
  to {
    opacity: 0;
  }
}
.toast--success {
  border-color: #47d864;
}
.toast--success .toast__icon {
  color: #47d864;
}
.toast--info {
  border-color: #2f86eb;
}
.toast--info .toast__icon {
  color: #2f86eb;
}
.toast--waning {
  border-color: orange;
}
.toast--waning .toast__icon {
  color: orange;
}
.toast--error {
  border-color: red;
}
.toast--error .toast__icon {
  color: red;
}
.toast + .toast {
  margin-top: 24px;
}
.toast__icon {
  font-size: 24px;
}
.toast__close, .toast__icon {
  padding: 0 16px;
}
.toast__body {
  flex-grow: 1;
  max-width: 300px;
  min-width: 250px;
}
.toast__title {
  font-size: 16px;
  font-weight: 600;
  color: #333;
}
.toast__msg {
  font-size: 14px;
  color: #888;
  line-height: 1.5;
  margin-top: 6px;
}
.toast__close {
  font-size: 20px;
  color: #999;
  cursor: pointer;
}

.bill {
  padding: 20px 45px;
  background-color: #fff;
  border: 1px solid #d2d2dc;
}
.bill__header {
  margin: 26px 0;
}
.bill__header p {
  font-size: 24px;
  font-weight: 700;
  line-height: 28.8px;
  margin: 48px 0px;
  text-align: right;
}
.bill__content {
  border-top: 1px solid #d2d2dc;
  border-bottom: 1px solid #d2d2dc;
  padding: 58px 0;
  overflow-x: auto;
}
.bill__content .bill__main {
  min-width: 800px;
}
.bill__content table {
  width: 100%;
  border-collapse: collapse;
}
.bill__content table thead {
  background-color: #08113b;
}
.bill__content table thead tr > th {
  padding: 20px 15px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
}
.bill__content table tbody tr > td {
  padding: 15px;
  border-top: 1px solid #f3f3f3;
}
.bill__date {
  margin: 48px 0;
}
.bill__info {
  display: flex;
  justify-content: space-between;
}
.bill__info .customer__info .customer__position {
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
}
.bill__info .customer__info .customer__name {
  margin-top: 4px;
}
.bill__info .bill__receive {
  text-align: right;
}
.bill__info .bill__receive .receive__title {
  font-weight: 700;
  margin-bottom: 4px;
  font-size: 14px;
}
.bill__bottom {
  margin-top: 48px;
  position: relative;
}
.bill__bottom p {
  text-align: right;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
}
.bill__bottom .bill__action {
  position: absolute;
  right: 20px;
  bottom: -44px;
}
.bill__footer {
  display: flex;
  margin-top: 48px;
  justify-content: end;
  gap: 0 8px;
}
.bill .btn-status {
  color: #fff;
  font-size: 12px;
  padding: 2px 6px;
  letter-spacing: 0.2px;
  display: inline-block;
  border-radius: 2px;
}
.bill .btn-status--success {
  background-color: #00c689;
}
.bill .btn-status--pending {
  background-color: orange;
}
.bill .btn-status--close {
  background-color: red;
}

.login {
  max-width: 450px;
  background-color: #fff;
  padding: 48px;
  margin: 100px auto 28px;
  box-shadow: 1px 0 20px rgba(0, 0, 0, 0.08);
  border-radius: 8px;
}
.login__form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px 0;
}
.login__logo {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login__logo .logo__link {
  font-size: 32px;
  color: #000;
  position: relative;
  text-decoration: none;
  font-weight: 700;
  letter-spacing: 0.2px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
}
.login__logo .logo__link b {
  position: relative;
  font-weight: 500;
  margin-right: 18px;
  font-size: 32px;
  color: #fff;
  z-index: 1;
}
.login__logo .logo__link b img {
  width: 22px;
  height: auto;
  object-fit: cover;
}
.login__logo .logo__link b::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  border-radius: 1% 50% 50% 50%;
  background-color: #ccc;
  z-index: -1;
}
.login__action {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.login__action .remember__login {
  display: flex;
  align-items: center;
  gap: 0 4px;
  color: pointer;
}
.login__action .remember__login label {
  color: #9b9b9b;
  font-size: 14px;
  line-height: 21px;
}
.login__action .register__link {
  text-decoration: none;
  color: #555;
  transition: all 0.3s ease;
}
.login__action .register__link:hover {
  text-decoration: underline;
}
.login__facebook i {
  font-size: 12px;
  margin-right: 8px;
}